@import url(https://fonts.googleapis.com/css?family=Gruppo&display=swap);

:root{
  --orange: #e8833e;
  --text-color: #f0f0f0;
  --background-color: #303030;
  --main-box-shadow: 5px 5px 10px #1f1f1f, 
             -5px -5px 10px #414141;
  --clicked-box-shadow: inset 5px 5px 10px #1f1f1f, 
             inset -5px -5px 10px #414141;
  --button-size: 90px;
  --button-gap: calc(var(--button-size) / 8);
  --main-width: calc(var(--button-size) * 4 + var(--button-size) / 2);
  --long-button: calc(var(--button-size) * 2 + (var(--button-size) / 8));
  --button-text-size: 3rem;
  --transition: .3s ease;
}

.light{
  --text-color: #616161;
  --background-color: #e3e0ed;
  --main-box-shadow: 5px 5px 10px #94929a, 
             -5px -5px 10px #ffffff;
  --clicked-box-shadow: inset 5px 5px 10px #94929a, 
             inset -5px -5px 10px #ffffff;
}

@media screen and (max-width: 600px){
  :root{
  --button-size: 70px;
  --button-text-size: 2rem;
  }
}

@media screen and (max-width: 400px){
  :root{
  --button-size: 60px;
  --button-text-size: 1.5rem;
  }
}

.App{
  font-family: 'Gruppo', cursive;
  font-weight: bold;
  background: #303030;
  background: var(--background-color);
  box-shadow: 5px 5px 10px #1f1f1f, 
             -5px -5px 10px #414141;
  box-shadow: var(--main-box-shadow);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 1rem;
  transition: .3s ease;
  transition: var(--transition);
  border-radius: 2rem;
}

.App *{
  transition: inherit;
}

body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root{
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background: #57606f;
}


.button-box{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: var(--button-gap);
    width: var(--main-width)
}

.div{
  font-family: "helvetica";
  color: var(--text-color);
  font-size: var(--button-text-size);
  width: var(--button-size);
  height: var(--button-size);
  background: var(--background-color);
  border: none;
  outline: none;

  border-radius: var(--button-size);
  box-shadow:  var(--main-box-shadow);
  display: flex;
  align-items: center;
  justify-content: center;
}

.div:active{
  
  box-shadow:var(--clicked-box-shadow); 
  font-size: calc(var(--button-text-size) * 0.95);
}

.double{
  width: var(--long-button);
  grid-column: 1/3;
  justify-content: flex-start;
  padding-left: 2rem;
}

.right{
  color: var(--orange);
}

.equals{
  background: var(--orange);
  color: var(--text-color);
}
.display{
    height: 150px;
    color: var(--text-color);
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: baseline;
    /* padding-top: 1rem; */
    font-size: 2rem;
    /* border: solid; */
}

.previous{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    word-wrap: break-word;
    width: var(--main-width);
    text-align: right;
}

.current{
    font-size: 4.5rem;
    color: var(--orange);
    word-wrap: break-word;
    width: var(--main-width);
    text-align: right;
}

.toggle-body{
  width: 50px;
  height: 25px;
  border-radius: 50px;
  padding: 2px;
  background: #303030;
  box-shadow: inset 5px 5px 10px #1f1f1f, 
             inset -5px -5px 10px #414141;
  /* transition: .2s ease; */
}

#toggle{
  display: none;
}

.indicator{
  height: 25px;
  width: 25px;
  border-radius: 50%;
  box-shadow: inset -5px -5px 10px #1f1f1f, 
             inset 5px 5px 10px #414141;
  background: #303030;
}

#toggle:checked ~ .toggle-body > .indicator{
    box-shadow: inset -5px -5px 10px #c1bec9, 
            inset 5px 5px 10px #ffffff;
    background: #e3e0ed;
  -webkit-transform: translateX(100%);
          transform: translateX(100%)
}

#toggle:checked ~ .toggle-body{
    background: #e3e0ed;
    box-shadow: inset 2px 2px 5px #94929a, 
             inset -2px -2px 5px #ffffff;
}

.switch{
    position: relative;
    left: -100%;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
